body, h1, p {
  margin: 0;
  padding: 0;
}

.container {
  max-width: 800px;
  background-color: #fff;
  border-radius: 5px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

h1 {
  color: #333;
  font-size: 24px;
}

p {
  color: #666;
  font-size: 16px;
}

a.goback-link {
  color: #007bff;
  text-decoration: none;
  transition: color .3s;
}

a.goback-link:hover {
  color: #0056b3;
}

.action-button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  padding: 10px 20px;
  font-size: 18px;
  transition: background-color .3s;
}

.action-button:hover {
  background-color: #0056b3;
}

.start-button {
  background-color: #4caf50;
}

.stop-button {
  background-color: #ff5722;
}

